import { addDays, format } from "date-fns";
import { observer } from "mobx-react-lite";
import { List, Spinner, Text } from "native-base";
import React, { useEffect, useState } from "react";
import { Button, ScrollView, StyleSheet, View } from "react-native";
import TaskItem from "../models/TaskItem";
import { useStores } from "../stores/RootStore";
import TaskListItem from "./TaskListItem";


const TodayTasksScreen = (props) => {
  const [dateSl,setDate] = useState(new Date());
  const { taskStore } = useStores();

  useEffect(()=> {
    taskStore.loadTaskList(props.user,dateSl);
  },[props.index, props.user,dateSl]);

  const onSelectItem = (task: TaskItem) => {
    task.toggleSelect();
  };

  const SaveSelectList = async() => {
    await taskStore.saveSelectList(props.user,dateSl);
  };

  const onDoneItem = (task: TaskItem) => {
    task.toggleComplete();
  };

  const SelectDay = (val) => {
    setDate(addDays(dateSl, val));
  };

  return (
     <ScrollView>
        <View style={{flexDirection: "column"}}>
          <View style={{flexDirection: "row", justifyContent: "space-around", marginTop: 20, marginBottom: 20 }}>
             <Button style={{size: ""}} title="<< Hôm qua" onPress={()=> SelectDay(-1)}/>
             <Button style={{size: ""}} title="Lưu việc đã chọn" onPress={()=> SaveSelectList()}></Button>
             <Button style={{size: ""}} title="Hôm sau >>" onPress={()=> SelectDay(1)}/>
          </View>
        </View>
        {taskStore.loading ? (<Spinner/> ):
        (
          <View style={{flexDirection: "column"}}>
            <Text style={styles.result} > Ngày {format(dateSl,"dd-MM-yyyy")}, 
              {props.user} đã chọn {taskStore.totalSelected} việc. {"\r\n"} Đã hoàn thành: {taskStore.totalCompleted} việc</Text>
            <Text style={{ fontSize: 18, fontWeight: "bold", marginLeft: 15, marginBottom: 20}}>Danh sách công việc: </Text>
            <List>
            {
              taskStore.sortedTaskList.map((taskItem, i) => (
                <TaskListItem key={taskItem.id} taskItem={taskItem} onSelectItem={onSelectItem} onDoneItem={onDoneItem} {...props} />
              ))
            }
            </List>
          </View>
        )}
    </ScrollView>
  );
};

const styles = StyleSheet.create({  
  result: {
    marginLeft: "10px",
    marginBottom: 20,
    fontSize: 18,
    fontWeight: "bold",
    color: "red"
  }
});


export default observer(TodayTasksScreen);